.msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--color-background);
    color: var(--text-color);
    padding: 20px;
    min-width: 80%;
    // min-width: 150px;
    border: 1px solid #ccc;
    border-radius: 5px;
    z-index: 10;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  .msgbox__content *{
    display: block;
  }
  
  .msgbox__content_title {
    font-size: 24px;
    margin-bottom: 10px;
    text-align: center;
    color: var(--red);
  }
  
  .msgbox__content_msg {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .msgbox__content__btns button {
    padding: 10px 20px;
    width: 100%;
    background-color: var(--light-red);
    border-radius: 5px;
    color: var(--white);
    border: none;
    cursor: pointer;
    text-transform: uppercase;
  }
  html[data-theme="dark"] .msgbox__content__btns button {
    background-color: var(--primary);

  }
